*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page-address .address-list, .page-nav .nav-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.container {
  width: 1200px;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
}

body {
  background-color: #ffffff;
  color: #212121;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  letter-spacing: 0.03em;
}

/* header */
.header-def .container {
  display: flex;
  align-items: center;
  border-bottom: 1px solid #ececec;
}
.header-def .link {
  display: flex;
  align-items: center;
  padding-top: 32px;
  padding-bottom: 32px;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.14;
  letter-spacing: 0.02em;
  text-decoration: none;
  color: #212121;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.header-def .link:hover, .header-def .link:focus {
  color: #2196f3;
}
.header-def .item {
  position: relative;
}

.page-nav {
  display: flex;
  align-items: center;
}
.page-nav .nav-list {
  display: flex;
  margin-left: 93px;
}
.page-nav .item:not(:first-child) {
  margin-left: 50px;
}
.page-nav .studio,
.page-nav .portfolio,
.page-nav .contacts {
  color: #2196f3;
}
.page-nav .studio::after,
.page-nav .portfolio::after,
.page-nav .contacts::after {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 4px;
  border-radius: 2px;
  background-color: #2196f3;
}

.page-address {
  margin-left: auto;
}
.page-address .address-list {
  display: flex;
  align-items: center;
}
.page-address .icon {
  margin-right: 10px;
  fill: currentColor;
}
.page-address .item:last-child {
  margin-left: 50px;
}
.page-address .reach {
  color: #757575;
}

/* company logo */
.logo {
  font-family: "Raleway", sans-serif;
  font-weight: 700;
  font-size: 26px;
  line-height: 1.38;
  letter-spacing: 0.03em;
  text-decoration: none;
  color: #212121;
}
.logo-blue {
  color: #2196f3;
}

/* footer */
footer {
  background-color: #2f303a;
}
footer .container {
  padding-top: 60px;
  padding-bottom: 60px;
  display: flex;
  align-items: baseline;
}

.footer-section-1 .logo {
  color: #ffffff;
}
.footer-section-1 .contact {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  margin-top: 20px;
}
.footer-section-1 .adress {
  margin-top: 0px;
  margin-bottom: 0px;
  color: #ffffff;
  font-style: normal;
  font-size: 14px;
  line-height: 1.71;
  letter-spacing: 0.03em;
}
.footer-section-1 .link {
  margin-top: 9px;
  color: #ffffff99;
  font-style: normal;
  font-size: 14px;
  line-height: 1.71;
  letter-spacing: 0.03em;
  text-decoration: none;
}

.footer-section-2 {
  margin-left: 70px;
}
.footer-section-2 .text {
  margin-top: 0px;
  margin-bottom: 20px;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.14;
  text-transform: uppercase;
  letter-spacing: 0.03em;
  color: #ffffff;
}
.footer-section-2 .foot-link {
  display: flex;
  align-items: center;
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.footer-section-2 .foot-item:not(:first-child) {
  margin-left: 10px;
}
.footer-section-2 .icon-link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 44px;
  height: 44px;
  border-radius: 50%;
  line-height: 0px;
  background-color: #ffffff1a;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.footer-section-2 .icon-link:hover, .footer-section-2 .icon-link:focus {
  background-color: #2196f3;
}
.footer-section-2 .icon {
  fill: #ffffff;
}

.footer-section-3 {
  margin-left: 93px;
}
.footer-section-3 .text {
  margin-top: 0px;
  margin-bottom: 20px;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.14;
  text-transform: uppercase;
  letter-spacing: 0.03em;
  color: #ffffff;
}
.footer-section-3 .foot-form {
  display: flex;
}
.footer-section-3 .foot-input {
  padding: 0px 16px;
  margin-right: 12px;
  width: 358px;
  height: 50px;
  border: 1px solid rgba(255, 255, 255, 0.3);
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.15));
  border-radius: 4px;
  outline: transparent;
  background-color: transparent;
  color: rgba(255, 255, 255, 0.6);
}
.footer-section-3 .sign-button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 200px;
  height: 50px;
  background-color: #2196f3;
  border-color: transparent;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  border-radius: 4px;
  cursor: pointer;
}
.footer-section-3 .sign-name {
  margin-right: 10px;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.87;
  letter-spacing: 0.06em;
  color: #ffffff;
}
.footer-section-3 .sign-icon {
  fill: #ffffff;
}

